<template>
  <div>
    <section class="pl-cart">
      <div class="container">
        <div class="p-cart__body">
          <div class="p-cart__block"
            v-for="(product, key, idx) in data.childrens"
            :key="idx"
          >
            <div class="p-cart__block__img">
              <img :src="product.img" :alt="product.name" />
            </div>
            <p class="p-cart__block__p">{{ product.name }}</p>
            <a class="p-cart__link" :href="key | getLink" />
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: 'ProductionList',
  props: ['data'],
  data() {
    return {
    };
  },
  filters: {
    getLink(key) {
      let path = window.location.pathname;

      if (path[path.length - 1] != '/')
        path = path + '/';
      
      return path + key;
    }
  },
  created() {
    console.log(this.data);
  },
  methods: {
  }
}
</script>

